<template>
  <div id="maphome">
    <div id="home-1">
      <div id="myMap"></div>
    </div>
    <canvas id="home-cartoon"></canvas>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {

      }
    },
    computed: {},
    mounted() {
      this.initCanvas();
      this.initLeaflet();
    },
    watch: {},
    methods: {
      // 初始化
      initCanvas() {
        document.addEventListener('touchmove', function (e) {
          e.preventDefault()
        })
        var c = document.getElementsByTagName('canvas')[0],
          x = c.getContext('2d'),
          pr = window.devicePixelRatio || 1,
          w = window.innerWidth,
          h = window.innerHeight,
          f = 90,
          q,
          m = Math,
          r = 0,
          u = m.PI * 2,
          v = m.cos,
          z = m.random
        c.width = w * pr
        c.height = h * pr
        x.scale(pr, pr)
        x.globalAlpha = 0.6

        function i() {
          x.clearRect(0, 0, w, h)
          q = [{
            x: 0,
            y: h * .7 + f
          }, {
            x: 0,
            y: h * .7 - f
          }]
          while (q[1].x < w + f) d(q[0], q[1])
        }

        function d(i, j) {
          x.beginPath()
          x.moveTo(i.x, i.y)
          x.lineTo(j.x, j.y)
          var k = j.x + (z() * 2 - 0.25) * f,
            n = y(j.y)
          x.lineTo(k, n)
          x.closePath()
          r -= u / -50
          x.fillStyle = '#' + (v(r) * 127 + 128 << 16 | v(r + u / 3) * 127 + 128 << 8 | v(r + u / 3 * 2) * 127 + 128).toString(
            16)
          x.fill()
          q[0] = q[1]
          q[1] = {
            x: k,
            y: n
          }
        }

        function y(p) {
          var t = p + (z() * 2 - 1.1) * f
          return (t > h || t < 0) ? y(p) : t
        }
        document.onclick = i
        document.ontouchstart = i
        i()
      },
      initLeaflet() {
        var mymap = L.map('myMap', {
          center: [39.916042, 116.390694],
          zoom: 14,
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        // Mapbox地图
        // L.tileLayer(
        //   'https://api.mapbox.com/styles/v1/kengqiangxia/cj5jbah540hlj2rpgh3xptiek/tiles/256/{z}/{x}/{y}?access_token=pk.eyJ1Ijoia2VuZ3FpYW5neGlhIiwiYSI6ImNqNWpiMWZ5ZTIxYzgyd3BrYTN2NDN5aXEifQ.bqY03lR_2LZ0fttPOJ4jyw', {
        //     attribution: '铿锵侠leaflet教程, Imagery © <a href="http://mapbox.com">Mapbox</a>',
        //   }).addTo(mymap);
        // OpenStreetMap地图
        L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
          attribution: '铿锵侠leaflet教程©OpenStreetMap'
        }).addTo(mymap);
      },
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #maphome {
    overflow-y: hidden;
  }

  #home-1 {
    margin: 0 auto;
    margin-top: 15%;
    text-align: center;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.647058823529412);
    font-family: '微軟正黑體 Bold', '微軟正黑體';
    font-weight: 700;
    font-style: normal;
    color: black;
    z-index: 10;
  }

  #home-cartoon {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

  #myMap {
    margin: 0 auto;
    height: 500px;
    width: 1200px;
  }

</style>



