import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import mapHome from '@/components/mapdemo/mapHome'
import mapOpenStreet from '@/components/mapdemo/mapOpenStreet'
import iView from 'iview'
import 'iview/dist/styles/iview.css' // 使用 CSS

Vue.use(Router)
Vue.use(iView)

export default new Router({
  routes: [{
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/mapview',
      name: 'mapHome',
      component: mapHome
    },
    {
      path: '/mapview/openstreet',
      name: 'mapOpenStreet',
      component: mapOpenStreet
    }
  ]
})



